import React from 'react';
import { Fragment } from 'react';
import Section from '../Section/Section';
import Options from './Options/Options';
import Notification from './Notification';
import Statistics from './Statistics/Statistics';

class Feedback extends React.Component {
  static defaultProps = {
    good: 0,
    neutral: 0,
    bad: 0,
    total: 0,
    positivePercentage: 0,
  };

  static propTypes = {};

  state = {
    good: this.props.good,
    neutral: this.props.neutral,
    bad: this.props.bad,
  };

  handleGoodFeedback = () => {
    this.setState(prevState => ({ good: prevState.good + 1 }));
  };
  handleNeutralFeedback = () => {
    this.setState(prevState => ({ neutral: prevState.neutral + 1 }));
  };
  handleBadFeedback = () => {
    this.setState(prevState => ({ bad: prevState.bad + 1 }));
  };

  countTotalFeedback = () =>
    Object.values(this.state).reduce((acc, curr) => acc + curr);

  countPositiveFeedbackPercentage = () => {
    // console.log(Math.round(this.state.good / this.countTotalFeedback()));
    return Math.round(this.state.good / this.countTotalFeedback());
  };

  render() {
    return (
      <Fragment>
        <Section title="Options">
          <Options
            className="Options"
            onHandleGoodFeedback={this.handleGoodFeedback}
            onHandleNeutralFeedback={this.handleNeutralFeedback}
            onHandleBadFeedback={this.handleBadFeedback}
          />
        </Section>
        {this.countTotalFeedback() === 0 ? (
          <Notification message="There is no feedback" />
        ) : (
          <Section title="Statistics">
            <Statistics
              className="Statistics"
              good={this.state.good}
              neutral={this.state.neutral}
              bad={this.state.bad}
              total={this.countTotalFeedback}
              positivePercentage={this.countPositiveFeedbackPercentage}
            />
          </Section>
        )}
      </Fragment>
    );
  }
}

export default Feedback;
